.App {
  width:100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

body{
  padding: 0;
  margin: 0;
}

.board{
  width: 500px;
  height: 500px;
  border: 1px solid black;
  background-color: aquamarine;
  display: flex;
  flex-direction: column;
}

.row{
  flex: 33%;
  display: flex;
  flex-direction: row;
}

.square{
  flex:33%;
  height: 100%;
  border: 1px solid black;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 30px;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}


.square:active{
  background-color: grey;
}

h4{
  margin: auto;
  display: flex;
  padding-left: 550px;
}